/* Модальное окно */
.modal {
  max-width: 490px;
  position: relative;
  display: none; }

.modal__feedback {
  padding-top: 30px;
  padding-right: 25px;
  padding-bottom: 25px;
  padding-left: 25px;
  overflow-x: hidden;
  color: #ffffff;
  background-color: #00b2FF; }
  .modal__feedback h2 {
    margin-top: 0;
    margin-bottom: 15px;
    font-size: 28px;
    line-height: 39px; }
  .modal__feedback p {
    width: 381px;
    padding-right: 25px; }
  .modal__feedback form {
    margin-top: 15px; }

.modal__feedback-form {
  width: 440px; }
  .modal__feedback-form form {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    margin-top: 30px; }

.modal__feedback-item {
  padding-top: 19px;
  padding-right: 15px;
  padding-bottom: 19px; }

.modal__feedback-item input[type="text"],
.modal__feedback-item input[type="tel"] {
  width: 100%;
  height: 50px;
  padding-left: 16px;
  font: inherit;
  color: #ffffff;
  background-color: rgba(255, 255, 255, 0.1);
  border: 1px solid transparent; }
  .modal__feedback-item input[type="text"]::-webkit-input-placeholder,
  .modal__feedback-item input[type="tel"]::-webkit-input-placeholder {
    color: #ffffff; }
  .modal__feedback-item input[type="text"]::-moz-placeholder,
  .modal__feedback-item input[type="tel"]::-moz-placeholder {
    color: #ffffff; }
  .modal__feedback-item input[type="text"]:-ms-input-placeholder,
  .modal__feedback-item input[type="tel"]:-ms-input-placeholder {
    color: #ffffff; }
  .modal__feedback-item input[type="text"]:hover,
  .modal__feedback-item input[type="tel"]:hover {
    border-color: rgba(255, 255, 255, 0.5);
    outline: none; }
  .modal__feedback-item input[type="text"]:focus,
  .modal__feedback-item input[type="tel"]:focus {
    border-color: #ffffff;
    outline: none; }

.modal__feedback-item textarea {
  width: 100%;
  height: 131px;
  padding-top: 10px;
  padding-right: 16px;
  resize: none;
  font: inherit;
  color: #ffffff;
  background-color: rgba(255, 255, 255, 0.1);
  border: 1px solid transparent; }
  .modal__feedback-item textarea::-webkit-input-placeholder {
    color: #ffffff; }
  .modal__feedback-item textarea::-moz-placeholder {
    color: #ffffff; }
  .modal__feedback-item textarea:-ms-input-placeholder {
    color: #ffffff; }
  .modal__feedback-item textarea:hover {
    border-color: rgba(255, 255, 255, 0.5);
    outline: none; }
  .modal__feedback-item textarea:focus {
    border-color: #ffffff;
    outline: none; }

.modal__feedback-consent {
  order: 1; }
  .modal__feedback-consent label {
    position: relative;
    display: block;
    width: 330px;
    padding-left: 44px;
    padding-top: 21px;
    font-size: 14px;
    line-height: 16px;
    background-color: transparent;
    cursor: pointer; }
    .modal__feedback-consent label::before {
      position: absolute;
      content: "";
      display: block;
      top: 21px;
      left: 0;
      align-self: flex-start;
      width: 17px;
      height: 16px;
      border: 1px solid #ffffff; }
    .modal__feedback-consent label::after {
      position: absolute;
      content: "";
      display: block;
      top: 24px;
      left: 8px;
      display: none;
      width: 5px;
      height: 7px;
      border: 1px solid white;
      border-left: none;
      border-top: none;
      transform: rotate(45deg); }
    .modal__feedback-consent label:hover {
      opacity: 0.8; }
    .modal__feedback-consent label:active {
      opacity: 0.6; }

.modal__feedback-consent input[type="checkbox"]:checked ~ label::after {
  display: block; }

.modal__feedback-consent input[type="checkbox"]:focus ~ label::before {
  background-color: rgba(255, 255, 255, 0.2); }

.modal__feedback-form button {
  flex-grow: 1;
  display: inline-block;
  margin-top: 3px;
  padding-top: 14px;
  padding-bottom: 14px;
  padding-left: 77px;
  padding-right: 77px;
  min-width: 109px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;
  background-color: transparent;
  border: 1px solid #ffffff;
  border-radius: 5px;
  cursor: pointer; }
  .modal__feedback-form button:hover {
    color: #00b2FF;
    background-color: #ffffff; }

.modal__close {
  position: absolute;
  top: 15px;
  right: 15px;
  width: 30px;
  height: 30px;
  font-size: 0;
  background-color: transparent;
  border: none;
  cursor: pointer; }
  .modal__close::before, .modal__close::after {
    position: absolute;
    content: "";
    display: block;
    top: 14px;
    left: 4px;
    width: 23px;
    height: 2px;
    background-color: #ffffff;
    transform: rotate(45deg); }
  .modal__close::after {
    transform: rotate(-45deg); }
