/* Модальное окно */
.modal {
  max-width: 490px;
  position: relative;

  display: none;
}

.modal__feedback {
  padding-top: 30px;
  padding-right: 25px;
  padding-bottom: 25px;
  padding-left: 25px;

  overflow-x: hidden;
  color: $white;

  background-color: $blue;

  h2 {
    margin-top: 0;
    margin-bottom: 15px;
    @include font(28, 39);
  }

  p {
    width: 381px;
    padding-right: 25px;
  }

  form {
    margin-top: 15px;
  }
}

.modal__feedback-form {
  width: 440px;

  form {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    margin-top: 30px;
  }
}

.modal__feedback-item {
  padding-top: 19px;
  padding-right: 15px;
  padding-bottom: 19px;
  //padding-left: 15px;
}

.modal__feedback-item input[type="text"],
.modal__feedback-item input[type="tel"] {
  width: 100%;
  height: 50px;
  //padding-top: 10px;
  //padding-right: 20px;
  //padding-bottom: 10px;
  padding-left: 16px;

  font: inherit;
  color: $white;
  background-color: rgba($white, 0.1);
  border: 1px solid transparent;

  &::-webkit-input-placeholder {
    color: $white;
  }

  &::-moz-placeholder {
    color: $white;
  }

  &:-ms-input-placeholder {
    color: $white;
  }

  &:hover {
    border-color: rgba($white, 0.5);
    outline: none;
  }

  &:focus {
    border-color: $white;
    outline: none;
  }
}

.modal__feedback-item textarea {
  width: 100%;
  height: 131px;
  padding-top: 10px;
  padding-right: 16px;
  //padding-bottom: 10px;
  //padding-left: 16px;
  resize: none;
  font: inherit;
  color: $white;
  background-color: rgba($white, 0.1);
  border: 1px solid transparent;

  &::-webkit-input-placeholder {
    color: $white;
  }

  &::-moz-placeholder {
    color: $white;
  }

  &:-ms-input-placeholder {
    color: $white;
  }

  &:hover {
    border-color: rgba($white, 0.5);
    outline: none;
  }

  &:focus {
    border-color: $white;
    outline: none;
  }
}

.modal__feedback-consent {
  order: 1;

  label {
    position: relative;
    display: block;
    width: 330px;
    padding-left: 44px;
    padding-top: 21px;

    @include font(14, 16);

    background-color: transparent;
    cursor: pointer;

    &::before {
      @include pseudo-elem;
      top: 21px;
      left: 0;
      align-self: flex-start;
      width: 17px;
      height: 16px;
      border: 1px solid $white;
    }

    &::after {
      @include pseudo-elem;
      top: 24px;
      left: 8px;
      display: none;

      @include size(5, 7);
      border: 1px solid white;
      border-left: none;
      border-top: none;
      transform: rotate(45deg);
    }

    &:hover {
      opacity: 0.8;
    }

    &:active {
      opacity: 0.6;
    }
  }
}

.modal__feedback-consent input[type="checkbox"]:checked ~ label::after {
  display: block;
}

.modal__feedback-consent input[type="checkbox"]:focus ~ label::before {
  background-color: rgba($white, 0.2);
}

.modal__feedback-form {
  button {
    flex-grow: 1;
    display: inline-block;
    margin-top: 3px;
    padding-top: 14px;
    padding-bottom: 14px;
    padding-left: 77px;
    padding-right: 77px;
    min-width: 109px;

    font-family: $roboto-condensed;
    font-weight: 700;
    @include font(14, 16);
    letter-spacing: 0.05em;
    text-align: center;
    text-transform: uppercase;
    color: $white;

    background-color: transparent;
    border: 1px solid $white;
    border-radius: 5px;

    cursor: pointer;

    &:hover {
      color: $blue;
      background-color: $white;
    }
  }
}

.modal__close {
  position: absolute;
  top: 15px;
  right: 15px;
  width: 30px;
  height: 30px;

  font-size: 0;

  background-color: transparent;
  border: none;
  cursor: pointer;

  //@include interactive();

  &::before,
  &::after {
    @include pseudo-elem;
    top: 14px;
    left: 4px;
    width: 23px;
    height: 2px;

    background-color: $white;
    transform: rotate(45deg);
  }

  &::after {
    transform: rotate(-45deg);
  }
}
